<template>
    <div align="center" class="mt-5">
        <b-card
            title="物品管理系統"
            img-src="https://picsum.photos/600/300/?image=24"
            img-alt="Image"
            img-top
            tag="article"
            style="max-width: 20rem"
            class="mb-2"
            border-variant="info"
        >
            <b-card-text class="desc">
                這是物品借用系統雛形，功能尚待測試，介面也有待進一步美化。
            </b-card-text>

            <div v-if="ready">
                <p>{{ ready }}</p>
                <p><b-button variant=success to="/login">登入</b-button></p>
                <p><b-button @click="updateData()">重新下載資料</b-button></p>
            </div>
        </b-card>
    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    data() {
        return {
            ready: null,
        };
    },
    computed: {
        ...mapState({
            users: (state) => state.users,
            items: (state) => state.items,
        }),
    },
    async created() {
        if (this.users && this.items) {
            this.ready = "準備就緒"
            return;
        }
        await this.updateData()
    },
    methods: {
        async updateData() {
            try {
                await this.$store.dispatch("get_collection", "users");
                await this.$store.dispatch("get_collection", "items");
                await this.$store.dispatch("get_collection", "borrows");
                this.ready = "已準備就緒";
                alert("資料下載完畢！")
            } catch (e) {
                console.log("Error", e);
            }
        },
    },
};
</script>

<style scoped>
.desc {
    color: blue;
}
</style>