<template>
    <div class="panel w-50">
        <b-modal v-model="showInfo" :title="info.title" ok-only>
            {{ info.message }}
        </b-modal>
        <b-input class="mt-3" v-model="user.email" placeholder="Username/Email" />
        <b-input class="mt-3" type="password" v-model="user.password" placeholder="輸入密碼" />
        <p align="center">
            <b-button class="mt-3" variant="info" @click="login">登入</b-button>
        </p>
    </div>
</template>

<script>
import { mapState } from "vuex";
// import { fireDb, fireAuth } from '@/plugins/firebase'
export default {
    data() {
        return {
            showInfo: false,
            info: {
                title: '',
                message: '',
            },
            user: {
                email: '',
                password: '',
            },
        }
    },
    computed: {
        ...mapState({
            curuser: (state) => state.curuser,
            users: (state) => state.users,
        }),
        userlist() {
            if (!this.users) return [];
            return Array.from(Object.entries(this.users), ([key, value]) =>
                Object.assign(value, { id: key })
            );
        },
    },
    created() {
        if (this.curuser) this.$router.push('/borrow')
    },
    methods: {
        checkValid() {
            if (this.user.realname === ''||this.user.phone === '') {
                alert("Name/Phone should not be empty!")
                return false
            }
            if (this.admin) {
                if (this.user.username === '') {
                    alert("Username should not be empty!")
                    return false
                }
                if (this.user.password.length < 6) {
                    alert("Password should be at least 6 chars!")
                    return false
                }
                if (this.user.password === '') {
                    alert("Password should not be empty!")
                    return false
                }
                if (this.user.password !== this.user.repassword) {
                    alert("Password should be same!")
                    return false
                }
            }
            return true
        },
        async login() {
            try {
                let email = ''
                let result = this.userlist.filter(item => item.email == this.user.email)
                if (result.length == 0) {
                    result = this.userlist.filter(item => item.username == this.user.email)
                    if (result.length == 0) {
                        this.info = {
                            'title': '登入失敗',
                            'message': '查無此使用者帳號'
                        }
                        this.showInfo = true
                        return
                    } else {
                        email = result[0].email
                    }
                }
                try {
                    await this.$store.dispatch('login', {email: email, password: this.user.password})
                    this.$router.push('/borrow')
                } catch (e) {
                    this.info = {
                        'title': '登入失敗',
                        'message': '帳號或密碼錯誤'
                    }
                    this.showInfo = true
                    return
                }
            } catch(e) {
                console.log("Error", e)
            }
        },
    },
}
</script>
    
<style scoped>
.panel {
    margin: 12px auto 12px auto;
}
</style>